import React from 'react';
import PropTypes from 'prop-types';

import { useAppReducer } from '../../hooks/useAppReducer';

import ClientStore from './ClientStore';
import initialState from './initialState';
import ClientStoreReducer from './ClientStoreReducer';

function ClientStoreProvider(props) {
    const [state, dispatch] = useAppReducer(ClientStoreReducer, initialState);
    const value = { state, dispatch };

    return <ClientStore.Provider value={value}>{props.children}</ClientStore.Provider>;
}

ClientStoreProvider.propTypes = {
    children: PropTypes.node.isRequired,
};

export default ClientStoreProvider;
