import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

import Avatar from '@wikia/react-common/components/StyledAvatar.esm';

const Creator = styled.div`
    display: flex;

    @media ${({ theme }) => theme.media.small_down} {
        font-size: ${({ theme }) => theme.font_size.s};
    }
`;

const Questions = styled.div`
    font-size: ${({ theme }) => theme.font_size.base};

    @media ${({ theme }) => theme.media.small_down} {
        font-size: ${({ theme }) => theme.font_size.s};
    }
`;

const StyledAvatar = styled(Avatar)`
    margin-right: 3px;
    padding-right: 3px;
`;

const UserName = styled.div`
    align-self: center;
    padding-left: 6px;
`;

const Wrapper = styled.div`
    align-items: center;
    display: flex;
    flex: 1;
    justify-content: space-between;
`;

function Header({ creatorId, creatorName, questions }) {
    return (
        <Wrapper>
            <Creator>
                <StyledAvatar size={30} user={creatorId} />
                <UserName>{creatorName}</UserName>
            </Creator>
            <Questions>{questions.length} Questions </Questions>
        </Wrapper>
    );
}

Header.propTypes = {
    creatorId: PropTypes.string.isRequired,
    creatorName: PropTypes.string.isRequired,
    questions: PropTypes.array.isRequired,
};

export default Header;
