import { createStore, applyMiddleware, compose } from 'redux';
import thunk from 'redux-thunk';

import initialState from './initialState';
import QuizStoreReducer from './QuizStoreReducer';

const composeEnhancers = typeof window === 'undefined' ? compose : window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const QuizStore = createStore(QuizStoreReducer, initialState, composeEnhancers(applyMiddleware(thunk)));

export default QuizStore;
