import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import sample from 'lodash/sample';

const TIME_IS_UP = [
    'Better luck next question! 😩',
    'Ok, that one was really hard 😱',
    'Keep on going! 💨',
];

const INCORRECT_ANSWER = [
    'Better luck next question! 😩',
    'Ok, that one was really hard 😱',
    'Keep on going! 💨',
];

function getEncourageString(mode) {
    if (mode === 'timeIsUp') {
        return sample(TIME_IS_UP);
    }

    return sample(INCORRECT_ANSWER);
}

const Message = styled.div`
    padding-top: 24px;
    left: 50%;
    transform: translate(-50%, 0);
    position: absolute;
    text-align: center;
`;

function EncourageMessage({ mode }) {
    return <Message>{getEncourageString(mode)}</Message>;
}

EncourageMessage.propTypes = {
    mode: PropTypes.oneOf(['timeIsUp', 'incorrectAnswer']).isRequired,
};

export default EncourageMessage;
