import IconClock from '@wikia/react-common/icons/IconClockSmall.esm';
import PropTypes from 'prop-types';
import React from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';

import Modal from '../Modal';
import { setStateToInProgress } from '../../stores/QuizStore/QuizStoreActions';

import QuizCountDown from './QuizCountdown';
import Header from './Header';

const TitleScreenHeading = styled.div`
    color: ${({ theme }) => theme.color.white};
    font-size: ${({ theme }) => theme.font_size.xxxxl};
    line-height: 1.15;
    margin-top: 66px;
    padding-left: 30px;
    padding-right: 30px;
    text-align: center;

    @media ${({ theme }) => theme.media.small_down} {
        font-size: ${({ theme }) => theme.font_size.xxl};
        margin-top: 66px;
        padding-left: 12px;
        padding-right: 12px;
    }
`;

const Divider = styled.div`
    background: #fff;
    height: 2px;
    margin-left: 30px;
    margin-right: 30px;
    margin-top: 44px;

    @media ${({ theme }) => theme.media.small_down} {
        height: 1px;
        opacity: 0.5;
    }
`;

const QuizHelp = styled.div`
    font-size: ${({ theme }) => theme.font_size.base};
    font-weight: ${({ theme }) => theme.font_weight.medium};
    margin-top: 25px;
    text-align: center;
`;

const QuizHelpDescription = styled.div`
    font-size: ${({ theme }) => theme.font_size.base};
    font-weight: ${({ theme }) => theme.font_weight.light};
    line-height: 1.5;
    margin-left: 70px;
    margin-right: 70px;
    margin-top: 12px;
    text-align: center;

    @media ${({ theme }) => theme.media.small_down} {
        margin-left: 6px;
        margin-right: 6px;
    }

`;

const Clock = styled(IconClock)`
    padding-right: 6px;
    vertical-align: text-top;
`;

function TitleScreen({ state, dispatch }) {
    return (
        <Modal Header={<Header {...state} />} image={state.image}>
            <TitleScreenHeading>{state.title}</TitleScreenHeading>
            <Divider />
            <QuizHelp>
                <Clock height={18} fill="white" />
                This quiz is timed!
            </QuizHelp>
            <QuizHelpDescription>
                You’ll have 10 seconds to complete each question. If you don’t complete a question, it’s counted as a wrong answer. Good luck! The quiz starts in….
            </QuizHelpDescription>
            <QuizCountDown onComplete={() => dispatch(setStateToInProgress())} />
        </Modal>
    );
}

TitleScreen.propTypes = {
    dispatch: PropTypes.func.isRequired,
    state: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
    state,
});

const mapDispatchToProps = dispatch => ({
    dispatch,
});

const ConnectedTitleScreen = connect(
    mapStateToProps,
    mapDispatchToProps
)(TitleScreen);


export default ConnectedTitleScreen;
