import HideComponent from '@wikia/react-common/components/HideComponent.esm';
import PropTypes from 'prop-types';
import React from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';

import { advanceToNextQuestion, setSelectedAnswer } from '../../stores/QuizStore/QuizStoreActions';
import { CENTER_WITH_FLEX } from '../../utils/cssSnippets';
import Modal from '../Modal';

import { trackImpression, trackClick } from '../../utils/tracking/tracking';

import AnswersScreen from './AnswersScreen';
import CorrectAnswerScreen from './CorrectAnswerScreen';
import QuestionHeader from './QuestionHeader';
import IncorrectAnswerScreen from './IncorrectAnswerScreen';

const QuestionMain = styled.div`
    ${CENTER_WITH_FLEX}
    background: ${props => props.theme.color.white};
    color: ${props => props.theme.color.black};
    flex: 1;
    position: relative;
    padding: 12px;

    @media ${({ theme }) => theme.media.medium_up} {
        padding: 60px;
    }
`;

const QuestionSafeArea = styled.div`
    margin: 54px 0;
`;

const SCREENS = Object.freeze({
    QUESTION: 'question',
    CORRECT_ANSWER: 'correctAnswer',
    INCORRECT_ANSWER: 'incorrectAnswer',
});

function Question({ state, dispatch }) {
    const [answerId, setAnswerId] = React.useState(-1);
    const [view, setView] = React.useState(SCREENS.QUESTION);

    const index = state.currentQuestionIndex;
    const question = state.questions[index];

    React.useEffect(() => {
        trackImpression(state.currentQuestionIndex);
    }, [state.currentQuestionIndex]);

    const onAnswerTimeout = () => {
        trackImpression('qz_time_out');
        // TODO update question to incorrect
        // clear state
        setAnswerId(-1);
        dispatch(advanceToNextQuestion());
    };

    const onNext = () => {
        // clear state
        setAnswerId(-1);
        dispatch(advanceToNextQuestion());
    };

    const onAnswer = (questionId, answeredId) => {
        // a user selects an answer
        trackClick(state.currentQuestionIndex);

        setAnswerId(answeredId);

        const NEXT_VIEW = question.answers.findIndex(answer => (answer.id === answeredId && answer.isCorrect)) !== -1 ? SCREENS.CORRECT_ANSWER : SCREENS.INCORRECT_ANSWER;

        if (NEXT_VIEW === SCREENS.CORRECT_ANSWER) {
            trackImpression('correct');
        } else {
            trackImpression('wrong');
        }

        setView(NEXT_VIEW);

        dispatch(setSelectedAnswer(questionId, answeredId));
    };

    return (
        <Modal Header={<QuestionHeader />} image={state.image}>
            <QuestionMain>
                <QuestionSafeArea>
                    <HideComponent hide={view !== SCREENS.QUESTION}>
                        <AnswersScreen onAnswerTimeout={onAnswerTimeout} onAnswer={onAnswer} />
                    </HideComponent>
                    <HideComponent hide={view !== SCREENS.INCORRECT_ANSWER}>
                        <IncorrectAnswerScreen answerId={answerId} onTimeout={onNext} />
                    </HideComponent>
                    <HideComponent hide={view !== SCREENS.CORRECT_ANSWER}>
                        <CorrectAnswerScreen onNext={onNext} />
                    </HideComponent>
                </QuestionSafeArea>
            </QuestionMain>
        </Modal>
    );
}

Question.propTypes = {
    dispatch: PropTypes.func.isRequired,
    state: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
    state,
});

const mapDispatchToProps = dispatch => ({
    dispatch,
});

const ConnectedQuestion = connect(
    mapStateToProps,
    mapDispatchToProps
)(Question);

export default ConnectedQuestion;
