export const ADVANCE_TO_NEXT_QUESTION = 'ADVANCE_TO_NEXT_QUESTION';
export const UPDATE_SCREEN_STATE = 'UPDATE_SCREEN_STATE';
export const UPDATE_DATA_STATE = 'UPDATE_DATA_STATE';
export const SELECT_ANSWER = 'SELECT_ANSWER';
export const QUIZ_DATA_LOADED = 'QUIZ_DATA_LOADED';

export function setSelectedAnswer(questionId, answerId) {
    return {
        type: SELECT_ANSWER,
        questionId,
        answerId,
    };
}

export const SCREEN_STATES = {
    TITLE_SCREEN: 'TITLE_SCREEN',
    IN_PROGRESS: 'IN_PROGRESS',
    COMPLETED: 'COMPLETED',
};

export const QUIZ_DATA_STATES = {
    ERROR: 'ERROR',
    INITIAL: 'INITIAL',
    LOADED: 'LOADED',
    LOADING: 'LOADING',
};

export function quizDataLoaded(data) {
    return {
        data,
        type: QUIZ_DATA_LOADED,
    };
}

export function setDataStateToLoading() {
    return {
        type: UPDATE_DATA_STATE,
        dataState: QUIZ_DATA_STATES.LOADING,
    };
}

export function setDataStateToLoaded() {
    return {
        type: UPDATE_DATA_STATE,
        dataState: QUIZ_DATA_STATES.LOADED,
    };
}

export function setDataStateToError() {
    return {
        type: UPDATE_DATA_STATE,
        dataState: QUIZ_DATA_STATES.ERROR,
    };
}

export function setStateToTitle() {
    return {
        type: UPDATE_SCREEN_STATE,
        screenState: SCREEN_STATES.TITLE_SCREEN,
    };
}

export function setStateToInProgress() {
    return {
        type: UPDATE_SCREEN_STATE,
        screenState: SCREEN_STATES.IN_PROGRESS,
    };
}

export function setStateToCompleted() {
    return {
        type: UPDATE_SCREEN_STATE,
        screenState: SCREEN_STATES.COMPLETED,
    };
}

export function advanceToNextQuestion() {
    return {
        type: ADVANCE_TO_NEXT_QUESTION,
    };
}
