import Cookies from 'js-cookie';

import Config from '../../Config';

import fetchWithLogging from '../fetchWithLogging';

function createRequestURL(params) {
    const query = Object.keys(params)
        .map((key) => {
            const value = params[key];

            if (value !== null) {
                return `${encodeURIComponent(key)}=${encodeURIComponent(value)}`;
            }

            return null;
        })
        .filter(Boolean)
        .join('&');

    return `${Config.INTERNAL_TRACKING_BASE}/special/trackingevent?${query}`;
}

// todo move to react-common
export default function track(eventData) {
    const {
        action,
        category,
        dbName,
        isGDPRApproved,
        label,
        userId,
        value,
        wikiId,
    } = eventData;

    const internalEventData = {
        beacon: Cookies.get('wikia_beacon_id') || '_____error',
        c: wikiId,
        cb: Math.floor(Math.random() * 99999),
        ga_action: action || '',
        ga_category: category || '',
        ga_label: label || '',
        ga_value: value || '',
        session_id: Cookies.get('wikia_session_id') || '_____error',
        u: userId,
        x: dbName,
    };

    const url = createRequestURL(internalEventData);

    // send an event only if user has approved the GDPR
    if (isGDPRApproved) {
        fetchWithLogging(url, { mode: 'no-cors' });
    }
}
