import produce from 'immer';
import shuffle from 'lodash/shuffle';

import { trackImpression } from '../../utils/tracking/tracking';

import {
    ADVANCE_TO_NEXT_QUESTION, QUIZ_DATA_LOADED, QUIZ_DATA_STATES,
    SCREEN_STATES,
    SELECT_ANSWER, UPDATE_DATA_STATE,
    UPDATE_SCREEN_STATE,
} from './QuizStoreActions';

/**
 * Convert quiz's `questions.answer` from the service to the structure we want
 */
function convertQuizQuestionAnswerApi(answer) {
    return {
        id: parseInt(answer.id, 10),
        text: answer.text,
        reference: null, // TODO FIXME
        // isSelected: false, // TODO FIXME
        isCorrect: answer.isCorrect || false,
    };
}

/**
 * Convert quiz's `question` from the service to the structure we want
 */
function convertQuizQuestionApi(question) {
    return {
        id: parseInt(question.id, 10),
        text: question.text,
        type: question.type,
        answers: shuffle(question.answers.map(a => convertQuizQuestionAnswerApi(a))),
    };
}

/**
 * Convert quiz from the service to the structure we want
 */
function convertQuizApi(quiz) {
    return {
        id: parseInt(quiz.id, 10),
        title: quiz.title,
        image: quiz.image,
        creatorName: '<creator name>', // TODO FIXME
        creatorId: quiz.creatorId,
        siteId: quiz.siteId,
        questions: quiz.questions.map(q => convertQuizQuestionApi(q)),
    };
}

const QuizReducer = (state, action = { type: '' }) => produce(state, (draft) => {
    switch (action.type) {
        case ADVANCE_TO_NEXT_QUESTION:
            if (state.currentQuestionIndex >= state.questions.length - 1) {
                draft.screenState = SCREEN_STATES.COMPLETED;
                // fire tracking
                trackImpression('complete');
            } else {
                draft.currentQuestionIndex += 1;
            }
            return;

        case UPDATE_SCREEN_STATE:
            draft.screenState = action.screenState;
            return;

        case SELECT_ANSWER:
            const question = draft.questions.find(q => q.id === action.questionId);
            const answerToSelect = question.answers.find(e => e.id === action.answerId);
            answerToSelect.isSelected = true;
            return;

        case UPDATE_DATA_STATE:
            draft.dataState = action.dataState;
            return;

        case QUIZ_DATA_LOADED:
            // TODO create proper converter for Quiz when introducing models back
            draft.dataState = QUIZ_DATA_STATES.LOADED;
            draft.currentQuestionIndex = 0;
            const quizData = convertQuizApi(action.data);

            Object.keys(quizData).forEach((key) => {
                draft[key] = quizData[key];
            });
    }
});

export default QuizReducer;
