import PropTypes from 'prop-types';
import React from 'react';
import styled from 'styled-components';
import IconArrow from '@wikia/react-common/icons/IconArrowTiny.esm';
import { connect } from 'react-redux';

import Modal from '../Modal';
import { CENTER_WITH_FLEX } from '../../utils/cssSnippets';

import ShareCard from './ShareCard';

const Title = styled.h2`
    color: ${({ theme }) => theme.color.white};
    font-size: ${({ theme }) => theme.font_size.xxl};
    font-weight: ${({ theme }) => theme.font_weight.normal};
    line-height: 1.2;
    text-align: center;
`;

const ScoreCardContainer = styled.div`
    flex-direction: column;
    ${CENTER_WITH_FLEX}
`;

const NextQuiz = styled.a`
    align-items: center;
    cursor: pointer;
    display: flex;
    flex: 1;
    font-size: ${({ theme }) => theme.font_size.xs};
    font-weight: ${({ theme }) => theme.font_weight.bold};
    justify-content: flex-end;
    text-transform: uppercase;
`;

const Arrow = styled(IconArrow)`
    margin-left: 4px;
    transform: rotate(180deg);
`;

const Wrapper = styled.div`
`;

function ScoreCard({ state, onNextQuiz }) {
    return (
        <Modal Header={<NextQuiz onClick={onNextQuiz}>Next Quiz<Arrow height="13" fill="#fff" /></NextQuiz>} image={state.image}>
            <Wrapper>
                <ScoreCardContainer>
                    <Title>{state.title}</Title>
                    <ShareCard questions={state.questions} />
                </ScoreCardContainer>
            </Wrapper>
        </Modal>
    );
}

ScoreCard.propTypes = {
    onNextQuiz: PropTypes.func.isRequired,
    state: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
    state,
});

const ConnectedScoreCard = connect(
    mapStateToProps,
)(ScoreCard);

export default ConnectedScoreCard;
