import PropTypes from 'prop-types';
import React from 'react';
import { connect } from 'react-redux';

import { SCREEN_STATES } from '../stores/QuizStore/QuizStoreActions';
import { loadSuggestedQuiz } from '../stores/QuizStore/QuizStoreThunks';

import TitleScreen from './TitleScreen/TitleScreen';
import Question from './Question';
import ScoreCard from './ScoreCard/ScoreCard';

function StateHandler({ state, dispatch }) {
    const onNextQuiz = () => {
        dispatch(loadSuggestedQuiz());
    };

    if (state.screenState === SCREEN_STATES.TITLE_SCREEN) {
        return <TitleScreen />;
    }

    if (state.screenState === SCREEN_STATES.IN_PROGRESS) {
        return <Question key={state.currentQuestionIndex} />;
    }

    if (state.screenState === SCREEN_STATES.COMPLETED) {
        return <ScoreCard onNextQuiz={onNextQuiz} />;
    }

    console.error('Unknown screenState', state.screenState);
    return null;
}

StateHandler.propTypes = {
    dispatch: PropTypes.func.isRequired,
    state: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
    state,
});

const mapDispatchToProps = dispatch => ({
    dispatch,
});

const ConnectedStateHandler = connect(
    mapStateToProps,
    mapDispatchToProps
)(StateHandler);

export default ConnectedStateHandler;
