import merge from 'lodash/merge';
import React from 'react';
import PropTypes from 'prop-types';
import { ThemeProvider } from 'styled-components';

import generateTheme from '@wikia/react-common/theme/generateTheme.esm';

import initialTheme from './initialState';
import ThemeStore from './ThemeStore';
import ThemeStoreReducer from './ThemeStoreReducer';

// Provides the state of the theme to the app from the local context (ThemeStore)
// Passes the theme into the StyledComponents provider making the property "theme" available in all styled components
function ThemeStoreProvider({ children, theme }) {
    const generatedTheme = generateTheme(merge({}, initialTheme, theme));
    const [state, dispatch] = React.useReducer(ThemeStoreReducer, generatedTheme);

    const value = { state, dispatch };

    return (
        <ThemeStore.Provider value={value}>
            <ThemeProvider theme={state}>
                {children}
            </ThemeProvider>
        </ThemeStore.Provider>
    );
}

ThemeStoreProvider.propTypes = {
    children: PropTypes.node.isRequired,
    theme: PropTypes.object, // eslint-disable-line
};

ThemeStoreProvider.defaultProps = {
    theme: {},
};

export default ThemeStoreProvider;
