import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import HideComponent from '@wikia/react-common/components/HideComponent.esm';
import IconCheckmark from '@wikia/react-common/icons/IconCheckmark.esm';
import IconLinkTiny from '@wikia/react-common/icons/IconLinkTiny.esm';
import IconArrowTiny from '@wikia/react-common/icons/IconArrowTiny.esm';

import { connect } from 'react-redux';

import { CENTER_WITH_FLEX } from '../../utils/cssSnippets';

import QuestionTitle from './QuestionTitle';

const CorrectAnswerBox = styled.div`
    border-radius: 3px;
    box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.3);
    background-color: ${({ theme }) => theme.color.white};
    padding: 30px;
    text-align: center;
`;

const Icon = styled(IconCheckmark)`
    fill: ${({ theme }) => theme.color.success};
    height: 34px;
    width: 34px;
`;

const Text = styled.div`
    font-size: 30px; /* not in DS */
    color: ${({ theme }) => theme.color.success};
`;

const LinkIcon = styled(IconLinkTiny)`
    fill: inherit;
    width: 12px;
    height: 12px;
    margin-right: 6px;
`;

const LearnMore = styled.a`
    color: ${({ theme }) => theme.color.dark_gray};
    display: block;
    font-family: inherit;
    font-size: ${({ theme }) => theme.font_size.xs};
    font-weight: ${({ theme }) => theme.font_weight.bold};
    margin: 30px 0;
    text-decoration: none;
    text-transform: uppercase;
`;

const NextIcon = styled(IconArrowTiny)`
    fill: inherit;
    width: 12px;
    height: 12px;
    margin-left: 6px;
    transform: scaleX(-1);
`;

const NextQuestion = styled.button`
    background: ${({ theme }) => theme.color.white};
    border-radius: 3px;
    border: 1px solid ${({ theme }) => theme.color.dark_gray};
    bottom: 70px;
    color: ${({ theme }) => theme.color.dark_gray};
    cursor: pointer;
    font-family: inherit;
    font-size: ${({ theme }) => theme.font_size.xs};
    font-weight: ${({ theme }) => theme.font_weight.bold};
    left: 50%;
    padding: 12px 18px;
    position: absolute;
    text-transform: uppercase;
    transform: translate(-50%, 0);
    ${CENTER_WITH_FLEX}
`;

function CorrectAnswerScreen({ onNext, state }) {
    const question = state.questions[state.currentQuestionIndex];
    const answer = question.answers.find(a => a.isCorrect);

    return (
        <React.Fragment>
            <QuestionTitle />
            <CorrectAnswerBox>
                <Icon />
                <Text>{answer.text}</Text>
                <HideComponent hide={!answer.reference}>
                    <LearnMore href={answer.reference} target="_blank">
                        <LinkIcon />learn more
                    </LearnMore>
                </HideComponent>
            </CorrectAnswerBox>
            <NextQuestion onClick={onNext}>
                next question<NextIcon />
            </NextQuestion>
        </React.Fragment>
    );
}


CorrectAnswerScreen.propTypes = {
    onNext: PropTypes.func.isRequired,
    state: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
    state,
});

const ConnectedCorrectAnswerScreen = connect(
    mapStateToProps,
)(CorrectAnswerScreen);

export default ConnectedCorrectAnswerScreen;
