import GoogleAnalytics from 'react-ga';

// todo move to react-common
export default function track(eventData) {
    const {
        action,
        category,
        dbName,
        isGDPRApproved,
        label,
        pageType,
        userCategory,
        value,
        wikiId,
    } = eventData;

    const gaEventData = {
        action,
        category,
    };

    if (label) {
        gaEventData.label = label;
    }

    if (value) {
        gaEventData.value = parseInt(value, 10);
    }

    if (!isGDPRApproved) {
        GoogleAnalytics.set({ anonymizeIp: true });
    }


    GoogleAnalytics.set({
        dimension1: dbName,
        dimension5: userCategory,
        dimension8: pageType,
        dimension9: wikiId,
    });

    GoogleAnalytics.event(gaEventData);
}
