import { SET_ENVIRONMENT, CLOSE_MODAL, OPEN_MODAL } from './ClientStoreActions';

const ClientReducer = (state, action = { type: '' }) => {
    switch (action.type) {
        case SET_ENVIRONMENT:
            state.environment = action.environment;
            return;
        case CLOSE_MODAL:
            state.isOpen = false;
            return;
        case OPEN_MODAL:
            state.isOpen = true;
    }
};

export default ClientReducer;
