import React from 'react';
import PropTypes from 'prop-types';
import useInterval from '@wikia/react-common/hooks/useInterval.esm';

import EncourageMessage from './EncourageMessage';
import QuestionAnswers from './QuestionAnswers';
import QuestionTitle from './QuestionTitle';

function IncorrectAnswerScreen({ answerId, onTimeout }) {
    useInterval(onTimeout, 2000);

    return (
        <React.Fragment>
            <QuestionTitle />
            <QuestionAnswers answerId={answerId} />
            <EncourageMessage mode="incorrectAnswer" />
        </React.Fragment>
    );
}


IncorrectAnswerScreen.propTypes = {
    answerId: PropTypes.number.isRequired,
    onTimeout: PropTypes.func.isRequired,
};

export default IncorrectAnswerScreen;
