import get from 'lodash/get';

import Config from '../../Config';

import internalEventTrack from './InternalTracker';
import gaEventTrack from './GoogleAnalyticsTracker';

// todo all of these to react-common
const ANONYMOUS_USER_ID = 0;
const OPTED_OUT_USER_ID = 0;
const ANON_USER_CATEGORY = 'anon';
const KNOWN_USER_CATEGORY = 'user';
const TEST_PAGE_TYPE = 'Test Page';

// TODO make me prettier!
const state = {
    isGDPRApproved: false,
    userId: ANONYMOUS_USER_ID,
    userCategory: ANON_USER_CATEGORY, // GA is using this
    wikiId: 0,
    dbName: '',
    pageType: TEST_PAGE_TYPE,
};

/**
 * `options` are described in README, let's just grab what we need from those
 */
export function initializeTracking(options) {
    state.isGDPRApproved = !!get(options, 'user.isGDPRApproved');

    if (state.isGDPRApproved) {
        state.userId = get(options, 'user.id', ANONYMOUS_USER_ID);
        state.userCategory = KNOWN_USER_CATEGORY;
    } else {
        state.userId = OPTED_OUT_USER_ID;
        state.userCategory = ANON_USER_CATEGORY;
    }
    state.dbName = get(options, 'community.dbName', '');
    state.pageType = get(options, 'pageType', TEST_PAGE_TYPE);
    state.wikiId = get(options, 'community.id', 0);
}


export function trackEvent(data) {
    const event = {
        ...state,
        category: Config.TRACKING_APP_NAME,
        ...data,
    };

    if (state.pageType === TEST_PAGE_TYPE) {
        console.log('Track Event', event);
    }

    // we fire tracking only when approved, but this check is in internal tracker
    internalEventTrack(event);
    // we fire tracking always, GA is taking care of anonymizing
    gaEventTrack(event);
}

export function trackSimpleEvent(label, action) {
    trackEvent({
        label: label.toString(),
        action: action.toString(),
    });
}

export function trackImpression(label) {
    trackSimpleEvent(label, 'impression');
}

export function trackClick(label) {
    trackSimpleEvent(label, 'click');
}
