import get from 'lodash/get';

import fetchWithLogging from '../../utils/fetchWithLogging';

import {
    quizDataLoaded,
    setDataStateToError,
    setDataStateToLoading,
    setStateToTitle,
} from './QuizStoreActions';

export function loadInitialQuiz(initialSiteId) {
    return (dispatch) => {
        dispatch(setDataStateToLoading());

        const baseUrl = 'https://services.wikia-dev.us/quizzes/quizzes/suggested';

        return fetchWithLogging(`${baseUrl}?siteId=${initialSiteId}`)
            .then(response => response.json())
            .then((json) => {
                dispatch(quizDataLoaded(json));
            }).catch((e) => {
                console.error('Failed to load quiz. ', e);
                dispatch(setDataStateToError());
            });
    };
}

export function loadQuiz(quizId) {
    return (dispatch, getState) => {
        // if we already have the quiz loaded in the state, do nothing
        if (get(getState(), 'id') === quizId) {
            return false;
        }

        dispatch(setDataStateToLoading());

        const devTestUrl = `https://services.wikia-dev.us/quizzes/quizzes/${quizId}`;

        return fetchWithLogging(devTestUrl)
            .then(response => response.json())
            .then((json) => {
                dispatch(quizDataLoaded(json));
            }).catch((e) => {
                console.error('Failed to load quiz. ', e);
                dispatch(setDataStateToError());
            });
    };
}

export function loadSuggestedQuiz() {
    return (dispatch, getState) => {
        const baseUrl = 'https://services.wikia-dev.us/quizzes/quizzes/suggested';
        const { siteId } = getState();
        const currentQuizId = getState().id;
        dispatch(setDataStateToLoading());

        return fetchWithLogging(`${baseUrl}?siteId=${siteId}&currentQuizId=${currentQuizId}`)
            .then(response => response.json())
            .then((json) => {
                dispatch(quizDataLoaded(json));
                dispatch(setStateToTitle());
            }).catch((e) => {
                console.error('Failed to loadSuggestedQuiz: ', e);
                dispatch(setDataStateToError());
            });
    };
}
