import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import useInterval from '@wikia/react-common/hooks/useInterval.esm';

const QuizCountDownWrapper = styled.div`
    display: flex;
    justify-content: space-between;
    margin-left: 70px;
    margin-right: 70px;
    margin-top: 36px;

    @media ${({ theme }) => theme.media.small_down} {
        margin-left: 45px;
        margin-right: 45px;
    }
`;

const QuizCountDownNum = styled.div`
    font-size: 42px; // not in rc
    font-weight: ${({ theme }) => theme.font_weight.black};
    opacity: ${({ active }) => (active ? '1' : '0.3')};

    @media ${({ theme }) => theme.media.small_down} {
        font-size: 30px; // not in rc
    }

`;

function QuizCountDown({ onComplete }) {
    const [value, setValue] = React.useState(0);

    useInterval(() => {
        setValue(value + 1);

        if (value === 4) {
            onComplete();
        }
    }, 1000);

    // this can be generated but I think it's more clear manually written out
    return (
        <QuizCountDownWrapper>
            <QuizCountDownNum active={value === 0}>5</QuizCountDownNum>
            <QuizCountDownNum active={value === 1}>4</QuizCountDownNum>
            <QuizCountDownNum active={value === 2}>3</QuizCountDownNum>
            <QuizCountDownNum active={value === 3}>2</QuizCountDownNum>
            <QuizCountDownNum active={value === 4}>1</QuizCountDownNum>
        </QuizCountDownWrapper>
    );
}

QuizCountDown.propTypes = {
    onComplete: PropTypes.func.isRequired,
};

export default QuizCountDown;
