import withErrorBoundaryFromRC from '@wikia/react-common/hocs/withErrorBoundary.esm';

import Config from '../Config';

export default function withErrorBoundary(WrappedComponent, boundaryName, FallBackComponent) {
    return withErrorBoundaryFromRC(WrappedComponent, {
        name: boundaryName,
        appName: Config.NAME,
        appVersion: Config.VERSION,
        fallbackComponent: FallBackComponent });
}
