import PropTypes from 'prop-types';
import React from 'react';
import { connect } from 'react-redux';

import ClientStore from '../context/ClientStore/ClientStore';
import withProviders from '../hocs/withProviders';
import { QUIZ_DATA_STATES } from '../stores/QuizStore/QuizStoreActions';
import { initializeTracking } from '../utils/tracking/tracking';
import { loadQuiz } from '../stores/QuizStore/QuizStoreThunks';
import { openModal, closeModal } from '../context/ClientStore/ClientStoreActions';

import QuizModalWrapper from './QuizModalWrapper';

/**
 * Component exported from the library
 */
function QuizController({ quizId, open, options, reduxState, reduxDispatch }) {
    // if there's an `options` prop that means the quiz is running in the React env (and we need to initialize tracking)
    if (typeof options !== 'undefined') {
        initializeTracking(options);
    }

    const { dispatch } = React.useContext(ClientStore);

    // on mount and when the quiz changes, load new data
    React.useEffect(() => {
        reduxDispatch(loadQuiz(quizId));
    }, [quizId]);

    if (reduxState.dataState !== QUIZ_DATA_STATES.LOADED) {
        return null;
    }

    // check if we should start with open or closed modal
    if (open) {
        dispatch(openModal());
    } else {
        dispatch(closeModal());
    }

    return <QuizModalWrapper />;
}

QuizController.propTypes = {
    open: PropTypes.bool,
    options: PropTypes.object,
    quizId: PropTypes.number.isRequired,
    reduxDispatch: PropTypes.func.isRequired,
    reduxState: PropTypes.object.isRequired,
};

QuizController.defaultProps = {
    open: false,
    options: undefined,
};

const mapStateToProps = state => ({
    reduxState: state,
});

const mapDispatchToProps = dispatch => ({
    reduxDispatch: dispatch,
});

const ConnectedQuizController = connect(
    mapStateToProps,
    mapDispatchToProps
)(QuizController);

export default withProviders(ConnectedQuizController);
