import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import colorAlpha from 'color-alpha';
import ReactModal from 'styled-react-modal';
import Image from '@wikia/react-common/components/Image';

import ClientStore from '../../context/ClientStore/ClientStore';
import { closeModal } from '../../context/ClientStore/ClientStoreActions';

const StyledModal = ReactModal.styled`
    background: ${({ theme }) => (colorAlpha(theme.contentTypes.color.primaryColor, 0.8))};
    box-shadow: 0 8px 12px 0 rgba(0, 0, 0, 0.3);
    height: 100vh;
    max-height: 100vh;
    max-width: 100vw;
    overflow-y: auto;
    position: relative;
    width: 100vw;

    @media screen and (min-height: 716px) {
        border-radius: 4px;
        height: 716px;
    }

    @media screen and (min-width: 700px) {
        border-radius: 4px;
        width: 700px;
    }
`;

const Background = styled(Image)`
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
`;

const ModalContent = styled.div`
    color: ${({ theme }) => theme.color.white};
    display: grid;
    grid-template-rows: 55px 1fr;
    height: 100%;
    padding: 12px;

    @media ${({ theme }) => theme.media.medium_up} {
        padding: 30px;
    }
`;

const ModalHeader = styled.div`
    display: flex;
    align-items: center;
`;

const ModalBody = styled.div`
    display: flex;
    flex-direction: column;
`;

function Modal({ children, Header, noPadding, image }) {
    const { state, dispatch } = React.useContext(ClientStore);

    const onEscapeKeydown = () => dispatch(closeModal());
    const imgUrl = `https://static.wikia.nocookie.net/${image}/smart/width/1060/height/596?format=original`;

    return (
        <StyledModal
            isOpen={state.isOpen}
            // onBackgroundClick={onBackgroundClick}
            onEscapeKeydown={onEscapeKeydown}
        >
            <Background src={imgUrl} alt="" />
            <ModalContent noPadding={noPadding}>
                <ModalHeader>{Header}</ModalHeader>
                <ModalBody>{children}</ModalBody>
            </ModalContent>
        </StyledModal>
    );
}

Modal.propTypes = {
    children: PropTypes.node.isRequired,
    Header: PropTypes.node,
    image: PropTypes.string,
    noPadding: PropTypes.bool,
};

Modal.defaultProps = {
    noPadding: false,
    Header: null,
    image: '',
};

export default Modal;
