import React from 'react';
import { Provider } from 'react-redux';

import ThemeStoreProvider from '../context/ThemeStore/ThemeStoreProvider';
import ClientStoreProvider from '../context/ClientStore/ClientStoreProvider';

import QuizStore from '../stores/QuizStore/QuizStore';

export default function withProviders(WrappedComponent) {
    function Component(props) {
        return (
            <Provider store={QuizStore}>
                <ClientStoreProvider>
                    <ThemeStoreProvider>
                        <WrappedComponent {...props} />
                    </ThemeStoreProvider>
                </ClientStoreProvider>
            </Provider>
        );
    }

    return Component;
}
