import PropTypes from 'prop-types';
import React from 'react';
import styled from 'styled-components';

import { connect } from 'react-redux';

import { ONE_LINE_ELLIPSIS } from '../../utils/cssSnippets';

const Wrapper = styled.div`
    align-items: center;
    color: ${({ theme }) => theme.color.white};
    display: flex;
    flex: 1;
    font-size: ${({ theme }) => theme.font_size.base};
    justify-content: space-between;
`;

const Title = styled.div`
    flex: 1;
    font-weight: ${({ theme }) => theme.font_weight.bold};
    letter-spacing: 0.3px;
    max-width: calc(100vw - (2 * 12px + 24px + 60px)); // 100vw - (mobile margins + width of Index below)
    ${ONE_LINE_ELLIPSIS}
`;

const Index = styled.div`
    margin-left: 24px;
    text-align: right;
    width: 60px;
`;


function QuestionHeader({ state }) {
    return (
        <Wrapper>
            <Title>{state.title}</Title>
            <Index>{state.currentQuestionIndex + 1} / {state.questions.length}</Index>
        </Wrapper>
    );
}

QuestionHeader.propTypes = {
    state: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
    state,
});

const ConnectedQuestionHeader = connect(
    mapStateToProps,
)(QuestionHeader);

export default ConnectedQuestionHeader;
